
import { defineComponent, ref, onMounted } from "vue";

import axios from "axios";

import Product from "../components/Product.vue";

import { useRouter } from "vue-router";

export default defineComponent({
  name: "Home",

  components: {
    Product,
  },

  setup() {
    let router: any = useRouter();

    //轮播图数据
    let bannerData: any = ref<Array<any>>([]);

    //热卖商品数据
    let hotProductData: any = ref<Array<any>>([]);

    //时间文本
    let timeText: any = ref<string>("");

    let username: any = ref<string>("");

    let isLogin: any = ref<boolean>(false);

    //获取轮播图数据
    let getBannerData: () => void = (): void => {
      axios({
        method: "get",
        url: "/banner",
      })
        .then((result: any): void => {
          if (result.data.code === 300) {
            bannerData.value = result.data.result;
          }
        })
        .catch((err: any): void => {
          
        });
    };

    //查看商品详情
    let viewProductDetail: (pid: string) => void = (pid: string): void => {
      router.push({ name: "Detail", params: { pid } });
    };

    //获取热卖商品
    let getHotProduct: () => void = (): void => {

      axios({
        method: "get",
        url: "/typeProducts",
        params: {
          key: "isHot",
          value: 1,
        },
      })
        .then((result: any): void => {
          if (result.data.code === 500) {
            result.data.result.forEach((item: any): void => {
              item.small_img = item.smallImg;
            });
            hotProductData.value = result.data.result;
          }
        })
        .catch((err: any): void => {
          
        });
    };

    //获取当前时间
    let getCurrentTime: () => void = (): void => {
      //定义时间段
      //00-06: 凌晨
      //07-10: 上午
      //11-14: 中午
      //15-18: 下午
      //19-23: 晚上

      let date: Date = new Date();
      //获取小时
      let hours: number = date.getHours();

      if (hours >= 19) {
        //晚上
        timeText.value = "晚上好";
      } else if (hours >= 15) {
        //下午
        timeText.value = "下午好";
      } else if (hours >= 11) {
        //中午
        timeText.value = "中午好";
      } else if (hours >= 7) {
        //上午
        timeText.value = "上午好";
      } else {
        //凌晨
        timeText.value = "凌晨好";
      }
    };

    //获取用户信息
    let getUserInfo: () => void = (): void => {
      axios({
        method: "get",
        url: "/findMy",
      })
        .then((result: any): void => {
          
          if (result.data.code === "A001") {
            username.value = result.data.result[0].nickName;
            isLogin.value = true;
          }
        })
        .catch((err: any): void => {
          
        });
    };

    //跳转登录
    let goPage: (name: string) => void = (name: string): void => {
      router.push({ name });
    };

    onMounted(() => {
      getBannerData();
      getHotProduct();
      getCurrentTime();
      getUserInfo();
    })

    return {
      bannerData,
      hotProductData,
      timeText,
      username,
      isLogin,
      goPage,
      viewProductDetail
    };
  },
});
